#board {
    display: grid;
    background-color: antiquewhite;
    /* width: 490px;
    height: 490px; */
    /* grid-template-columns: repeat(7, 1fr);
    grid-template-rows: repeat(7, 1fr); */
}

.cell::before, .cell::after, .left::before, .left::after,
.top::before, .top::after, .right::before, .right::after,
.bot::before, .bot::after, .rt::before, .rt::after,
.lt::before, .lt::after, .lb::before, .lb::after,
.rb::before, .rb::after
 {
    content: "";
    position: absolute;
    background-color: black;
}

.cell::before {
    width: 2px;
    height: 100%;
    top: 0;
    left: 50%;
}

.cell::after {
    width: 100%;
    height: 2px;
    top: 50%;
    left: 0;
}

.left::before {
    width: 2px;
    height: 100%;
    top: 0;
    left: 50%;
}

.left::after {
    width: 50%;
    height: 2px;
    top: 50%;
    left: 50%;
}

.right::before {
    width: 2px;
    height: 100%;
    top: 0;
    left: 50%;
}

.right::after {
    width: 50%;
    height: 2px;
    top: 50%;
    right: 50%;
}

.top::before {
    width: 2px;
    height: 50%;
    top: 50%;
    left: 50%;
}

.top::after {
    width: 100%;
    height: 2px;
    top: 50%;
    left: 0;
}

.bot::before {
    width: 2px;
    height: 50%;
    bottom: 50%;
    left: 50%;
}

.bot::after {
    width: 100%;
    height: 2px;
    top: 50%;
    left: 0;
}

.lt::before {
    width: 2px;
    height: 50%;
    top: 50%;
    left: 50%;
}

.lt::after {
    width: 50%;
    height: 2px;
    top: 50%;
    left: 50%;
}

.rt::before {
    width: 2px;
    height: 50%;
    top: 50%;
    left: 50%;
}

.rt::after {
    width: 50%;
    height: 2px;
    top: 50%;
    right: 50%;
}

.lb::before {
    width: 2px;
    height: 50%;
    bottom: 50%;
    left: 50%;
}

.lb::after {
    width: 50%;
    height: 2px;
    top: 50%;
    left: 50%;
}

.rb::before {
    width: 2px;
    height: 50%;
    bottom: 50%;
    left: 50%;
}

.rb::after {
    width: 53%;
    height: 2px;
    top: 50%;
    right: 47%;
}



.b::after {
    content: "";
    position: absolute;
    background-color: black;
    align-content: center;
    border: 2px solid antiquewhite;
    width: 80%;
    height: 80%;
    border-radius: 50%;
}

.w::after {
    content: "";
    position: absolute;
    background-color: white;
    align-content: center;
    border: 2px solid black;
    width: 80%;
    height: 80%;
    border-radius: 50%;
}